//if you like, change this (your brand color)
$base-color: #28497c; // #28497c


//PLEASE, no changes from here
//$base-gray: #3c454e; // #3c454e
//calculated base gray
$base-gray: hsl(hue($base-color),
                saturation(#3c454e),
                lightness(#3c454e));

$brand-color-dark: $base-color;
$brand-color-darker: hsl(hue($base-color),
                         (saturation($base-color) + 5.4%),
                         (lightness($base-color) - 4%)); // #1f3f70;

$brand-color-light: hsl(hue($base-color),
                        (saturation($base-color) - 6%),
                        (lightness($base-color) + 6.5%)); // #36598f;

$brand-color-lighter: hsl((hue($base-color) + 2.5),
                          (saturation($base-color) - 25.5%),
                          (lightness($base-color) + 31%)); // #899ab9;
/* This code calculates another activity color in case you dont wanna stick with red

$active-color: hsv((hsvhue($red) - hsvhue($base-color) + hsvhue($origin-base-color)),
hsvsaturation($red),
hsvvalue($red));
*/

$black: #000000;
